import { Injectable } from '@angular/core';
import { Plugin, Cordova, IonicNativePlugin } from '@ionic-native/core';

/**
 * @name Bixolon Printer
 * @description
 * This plugin does something
 *
 * @usage
 * ```typescript
 * import { BixolonPrinter } from '@ionic-native/bixolon-printer';
 *
 *
 * constructor(private bixolonPrinter: BixolonPrinter) { }
 *
 * ...
 *
 *
 * this.bixolonPrinter.functionName('Hello', 123)
 *   .then((res: any) => console.log(res))
 *   .catch((error: any) => console.error(error));
 *
 * ```
 */
@Plugin({
  pluginName: 'BixolonPrinter',
  plugin: 'cordova-plugin-bixolon-print',
  pluginRef: 'BixolonPrint', // the variable reference to call the plugin, example: navigator.geolocation
  repo: '', // the github repository URL for the plugin
  platforms: ['Android', 'iOS'], // Array of platforms supported, example: ['Android', 'iOS']  
})

export class BixolonPrinter extends IonicNativePlugin {

  @Cordova()
  addLine(json: any): Promise<any> { return; }

  @Cordova()
  addHr(separator?: String): Promise<any> { return; }

  @Cordova({
    callbackOrder: 'reverse'
  })
  printText(config?: JSON): Promise<any> { return; }


  @Cordova({
    callbackOrder: 'reverse'
  })
  cutPaper(config: JSON): Promise<any> { return; }

  @Cordova({
    callbackOrder: 'reverse'
  })
  getStatus(printStatus: Boolean): Promise<any> { return; }

static decorators: DecoratorInvocation[] = [
{ type: Injectable },
];
/** @nocollapse */
static ctorParameters: () => ({type: any, decorators?: DecoratorInvocation[]}|null)[] = () => [
];
}

interface DecoratorInvocation {
  type: Function;
  args?: any[];
}
